import Title from "./Title";
import { useSelector } from "react-redux";

function Notice(){
    let data = useSelector(state => state.notice);
    return(
        <div className="notice">
            <div className="title">
                <Title propsValue="Notice"/>
                <a href="" className="more">새소식 모두 보기</a>
            </div>
            <div className="contents">
                <ul>
                    {data.map((d, i)=> <li key={i+1}>
                    <a href="">{d.title}
                    </a><span>{d.date}</span>
                    </li>)}
                </ul>
            </div>
        </div>
    );
}
export default Notice;