import SliderNavigation from "./SliderNavigation";
import { Swiper, SwiperSlide } from "swiper/react";
import { Grid } from "swiper/modules"
import 'swiper/css';
import 'swiper/css/grid';
import { useState } from "react";

function ShowingContents(props){
    let data = props.propsValue;
    let [swiper, setSwiper]=useState(null);
    let newData = data.filter(data => data !== undefined);

    let noShowing = {
        height: 200,
        textAlign: "center",
        lineHeight: "200px",
        fontSize: "20px",
        fontWeight: 600,
        color: "#333"
    }
    if ( newData.length === 0 ){
        return(
            <div className="contents">
                <Swiper className="swiper-container">
                    <p className="no_showing" style={noShowing}>현재 진행중인 전시가 없습니다.</p>
                </Swiper>
            </div>
            
        );
    }
    else {
        return(
            <div className="contents">
                <Swiper className="swiper-container subSwiper"
                slidesPerView={1.3}
                spaceBetween={20}
                grid={{
                    rows: 1
                }}
                modules={[Grid]}
                breakpoints={{
                    1130: {
                        slidesPerView: 3,
                        spaceBetween: 70,
                        grid: {
                            rows: 1
                        }
                    },
                    750: {
                        slidesPerView: 2,
                        spaceBetween: 70,
                        grid: {
                        rows: 2,
                        fill: "row"
                        }
                    }
                }}
                onSwiper={(swiper)=>{setSwiper(swiper)}}>
                        {newData.map((d, i)=> <SwiperSlide key={i+1}>
                        <a href="">
                            <div className="thumb"><img src={`${process.env.PUBLIC_URL}/assets/images/`+d.image} alt={d.alt} /></div>
                            <div className="text">
                                <strong className="title">{d.title}</strong>
                                <div className="desc">
                                    <span className="museum">{d.museum}</span>
                                    <span className="date">{d.date}</span>
                                </div>
                            </div>
                        </a>
                    </SwiperSlide>)} 
                </Swiper>
                <SliderNavigation propsValue={swiper}/>
            </div>
        );
    }
};
export default ShowingContents;