import Title from './Title'
import MuseumContents from "./MuseumContents";
import Notice from './Notice';
import Reservation from './Reservation'

function Museum(){
    return(
        <section id="sec1">
            <div className="top">
                <Title propsValue="미술관"/>
                <MuseumContents />
            </div>
            <div className="bottom">
                <Notice />
                <Reservation />
            </div>
        </section>
    );
}
export default Museum;