import { useEffect, useState } from "react";
import ShowingContents from "./ShowingContents";
import Title from "./Title";
import { useSelector } from "react-redux";

function Showing(){
    let data = useSelector(state => state.showing);
    let [ listData, setList ] = useState(data.content);
    useEffect(()=>{
        let categoryLi = document.querySelectorAll("#sec3 .slider .category li");
        categoryLi[0].classList.add("active");

        categoryLi.forEach((d , idx)=> {
            d.addEventListener("click", (e)=>{
                e.preventDefault();
                categoryLi.forEach(d => d.classList.remove("active"));
                e.currentTarget.classList.add("active");
                let contentList = data.content.map(d => {
                    if( idx === 0 ){
                        return d;
                    }
                    else{
                        if(data.category[idx] === d.museum){
                            return d
                        }
                    }
                });
                setList(contentList);
            })
        });
    }, []);
    return(
        <section id="sec3">
            <div className="inner">
                <div className="slider">
                    <Title propsValue="전시" />
                    <div className="category">
                        <ul>
                            {data.category.map((d, i)=><li key={i+1}><a href="">{d}</a></li>)}
                        </ul>
                        <a href="" className="more">전시 모두 보기</a>
                    </div>
                    <ShowingContents propsValue={listData}/>
                <div className="bottom">
                    <a href="" className="more">전시 모두 보기</a>
                </div>
                    </div>
            </div>
        </section>
    );
}
export default Showing;