import { useEffect } from "react";

function Tab(){
    useEffect(()=>{
        let tab = document.querySelector("header .tab");
        let tabCheck=false;
        let searchCheck=false;
        let searchArea = document.querySelector("header .search_area");
        let search = document.querySelector("header .utils .search");
        let mobileNav = document.getElementById("mobile");
        let winw=0;

        tab.addEventListener("click", (e)=>{
            searchArea.classList.remove("active");
            search.classList.remove("active");
            if(!tabCheck){
                e.currentTarget.classList.add("active");
                mobileNav.style.display="block";
                document.body.classList.add("fixed");
            }
            else{
                e.currentTarget.classList.remove("active");
                mobileNav.style.display="none";
                document.body.classList.remove("fixed");
            }

            tabCheck=tab.classList.contains("active");
        });

        window.addEventListener("resize", ()=>{
            winw=window.innerWidth;
            if(winw  > 1340 && tabCheck){
                tab.classList.remove("active");
                mobileNav.style.display="none";
                document.body.classList.remove("fixed");
            }
        });

        search.addEventListener("click", (e)=>{
            e.preventDefault();
            searchCheck=searchArea.classList.contains("active");
            tab.classList.remove("active");
            document.body.classList.remove("fixed");
            mobileNav.style.display = "none";
            tabCheck=false;
            if(!searchCheck){
                searchArea.classList.add("active");
                e.currentTarget.classList.add("active");
            }
            else{
                searchArea.classList.remove("active");
                e.currentTarget.classList.remove("active");
            }
        });
    });
    return(
        <div className="tab">
            <span></span>
            <span></span>
            <span></span>
        </div>
    );
}
export default Tab;