function FamilySite(props){
    let data = props.propsValue;
    return(
        <div className="familly">
            <a href="">관련사이트</a>
            <ul>
                {data.map((d, i)=><li key={i+1}><a href={d.href}>{d.label}</a></li>)}
            </ul>
        </div>
    );
}
export default FamilySite;