import { useEffect } from "react";
import Logo from "./Logo";
import Navigation from "./Navigation";
import Search from "./Search";
import Tab from "./Tab";
import Utils from "./Utils";
import gsap from "gsap";

function Header(){
    useEffect(()=>{
        let desktopNav=document.getElementById("desktop");
        let desktopNavLi=desktopNav.querySelectorAll("#desktop > ul > li");
        let navBg=document.querySelector(".nav_bg");
        let mobileNav=document.getElementById("mobile");
        let mobileNavLi=mobileNav.querySelectorAll("#mobile > ul > li");

        desktopNavLi.forEach(d => {
            d.addEventListener("mouseenter", (e)=>{
                e.currentTarget.classList.add("active");
                navBg.style.display="block";
                desktopNav.classList.add("active");
            });
            d.addEventListener("mouseleave", (e)=>{
                e.currentTarget.classList.remove("active");
                navBg.style.display="none";
                desktopNav.classList.remove("active");
            });
            d.addEventListener("focusin", (e)=>{
                if(!e.currentTarget.classList.contains("active")){
                    e.currentTarget.classList.add("active");
                navBg.style.display="block";
                desktopNav.classList.add("active");
                }
            });
            d.lastElementChild.lastElementChild.addEventListener("focusout", (e)=>{
                if(e.currentTarget.parentElement.parentElement.classList.contains("active")){
                    e.currentTarget.parentElement.parentElement.classList.remove("active");
                navBg.style.display="none";
                desktopNav.classList.remove("active");
                }
            });
        });
        mobileNavLi.forEach(d => {
            d.addEventListener("click", (e)=>{
                e.preventDefault();
                if(!e.currentTarget.classList.contains("active")){
                    mobileNavLi.forEach(d => d.classList.remove("active"));
                    e.currentTarget.classList.add("active");
                    gsap.to(document.querySelectorAll("header #mobile li ul"), {height: 0, duration: 0.3});
                    gsap.to(e.currentTarget.lastElementChild, {height: "auto", duration: 0.3});
                }
                else{
                    e.currentTarget.classList.remove("active");
                    gsap.to(e.currentTarget.lastElementChild, {height: 0, duration: 0.3});
                } 
                })
        })
    });
    return(
        <header>
            <Logo />
            <Navigation />
            <Tab />
            <Utils />
            <Search />
        </header>
    );
}
export default Header;