import FamilySite from "./FamilySite";
import FooterBottons from "./FooterBottons";
import FooterUtils from "./FooterUtils";
import SNS from "./SNS";
import Subscribe from "./Subscribe";

function FooterContents(props){
    let data = props.propsValue;
    return(
        <div className="top">
            <div className="inner">
                <div className="left">
                    <FooterBottons propsValue={data.link} />
                            <div className="left_middle">
                                <FamilySite propsValue={data.family} />
                                <SNS propsValue={data.sns} />
                            </div>
                        </div>
                <div className="right">
                    <Subscribe propsValue={data.subscribe} />
                    <FooterUtils propsValue={data.utils} />
                </div>
            </div>
        </div>
    );
}
export default FooterContents;