function SNS(props){
    let data = props.propsValue;
    return(
        <div className="sns">
            <ul>
                {data.map((d, i)=><li key={i+1}><a href={d.href}><img src={`${process.env.PUBLIC_URL}/assets/images/`+d.image} alt={d.alt} /></a></li>)}
            </ul>
        </div>
    );
}
export default SNS;