import { useEffect } from "react";
import Copyright from "./Copyright";
import FooterContents from "./FooterContents";
import { useSelector } from "react-redux";

function Footer(){
    let data = useSelector(state => state.footer);
    useEffect(()=>{
        let familly = document.querySelector("footer .familly");
        let famillyUl = familly.lastElementChild;
        let famillyCheck;

        let agreeArea = document.querySelector("footer .agree_area");
        let check = document.querySelector("footer .check");
        let realCheck= document.getElementsByName("hidden_agree")[0];
        let agreeCheck;
        
        familly.addEventListener("click", (e)=>{
            e.preventDefault();
            famillyCheck=familly.classList.contains("active");
            if(!famillyCheck){
                e.currentTarget.classList.add("active");
                famillyUl.style.display = "block";
            }
            else{
                e.currentTarget.classList.remove("active");
                famillyUl.style.display = "none";
            }
        });

        agreeArea.addEventListener("click", (e)=>{
            e.preventDefault();
            agreeCheck=check.classList.contains("on");
            if(!agreeCheck){
                check.classList.add("on");
                realCheck.checked = true;
            }
            else {
                check.classList.remove("on");
                realCheck.checked = false;
            }
        })
    })
    return(
        <footer>
            <FooterContents propsValue={data}/>
            <Copyright propsValue={data.copyright}/>
        </footer>
    );
}
export default Footer;