import { useSelector } from "react-redux";

function Reservation(){
    let data = useSelector(state => state.reserve);
    return(
        <div className="reserve">
            <a href="">
                <div className="background"><img src={`${process.env.PUBLIC_URL}/assets/images/`+data.image} alt={data.alt} /></div>
                <span>{data.span}</span>
            </a>
        </div>
    );
}
export default Reservation;