import { useSelector } from "react-redux";

function MuseumContents(){
    let data = useSelector(state => state.museum);
    return(
        <div className="contents">
            <ul>
                {data.map((d,i) => <li key={i+1}>
                <a href="">
                    <div className="thumb"><img src={`${process.env.PUBLIC_URL}/assets/images/`+d.image} alt={d.alt} /></div>
                    <div className="text">
                        <h3>{d.h3}</h3>
                        <p>{d.p}</p>
                    </div>
                </a>
            </li>)}
            </ul>
        </div>
    );
}
export default MuseumContents;