import { useEffect } from "react";
import { useSwiper } from "swiper/react";
import gsap from "gsap";

function SliderNavigation(props){
    let mainSwiper = useSwiper();
    let subSwiper=props.propsValue;
    useEffect(()=>{
        let mainNav = document.querySelectorAll("#main .navigation button");
        
        mainNav.forEach(d => {
            d.addEventListener("mouseenter", () => gsap.to(d, {opacity:1, duration: 0.3}));
            d.addEventListener("mouseleave", () => gsap.to(d, {opacity:0, duration: 0.3}));
        });

    });
    if(!subSwiper){
        return(
            <div className="navigation">
                <button className="prev" onClick={()=> mainSwiper.slidePrev()}>prev</button>
                <button className="next" onClick={()=> mainSwiper.slideNext()}>next</button>
            </div>
        );
    }
    else{
        return(
            <div className="navigation">
                <button className="prev" onClick={()=> subSwiper.slidePrev()}>prev</button>
                <button className="next" onClick={()=> subSwiper.slideNext()}>next</button>
            </div>
        );
    }
    
}
export default SliderNavigation;