function Subscribe(){
    return(
        <>
            <span>Subscribe to <span className="bold">MMCA Newsletter</span></span>
            <p>국립현대미술관의 다양한 소식을 가장 먼저 받아보세요.</p>
            <form>
                <div className="join_area">
                    <input id="email" type="text" placeholder="email" />
                    <button type="submit">Join</button>
                </div>
            </form>
            <div className="agree_area">
                <a href="">
                    <span className="check"></span>
                    <span className="info"> 개인정보 이용내역에 동의합니다.</span>
                </a>
                <input type="checkbox" name="hidden_agree" style={{display: "none"}} />
            </div>
        </>
    );
}
export default Subscribe;