import { Swiper, SwiperSlide } from 'swiper/react'
import 'swiper/css';
import SliderNavigation from './SliderNavigation';
import { useSelector } from 'react-redux';
import { useState } from 'react';


function MainSlider(){
    let data=useSelector(state => state.mainSlider);

	let [videoList, setVideoList]=useState(null);
	function videoInteraction(idx){
		for(let i=0; i<videoList.length; i++){
			if(i === idx){
				videoList[i].firstElementChild.play();
			}
			else{
				videoList[i].firstElementChild.pause();
				videoList[i].firstElementChild.currentTime=0;
			}
		}
	}
	
	return(
		<div className="main_slider">
				<Swiper className="swiper-container mainSwiper"
				sliderperview={1}
				spaceBetween={30}
				loop={true}
				onInit={swiper => {
					let mainSlider=document.querySelector(".main_slider");
					let swiperSlide=mainSlider.querySelectorAll(".swiper-slide");
			
					let videoList=[];
			
					for(let i=0; i<swiperSlide.length; i++){
						if(swiperSlide[i].firstElementChild.tagName !== "IMG"){
							videoList.push(swiperSlide[i]);
						}
					}

					videoList[swiper.realIndex].firstElementChild.play();

					setVideoList(videoList);
				}}
				onSlideChangeTransitionEnd={swiper => {
					videoInteraction(swiper.realIndex);
				}}
				>
					{data.map((d,i) =>
						d.video ? <SwiperSlide key={i+1}><video id={d.id} muted autoPlay playsInline loop poster={`${process.env.PUBLIC_URL}/assets/videos/`+d.poster} src={`${process.env.PUBLIC_URL}/assets/videos/`+d.video}></video></SwiperSlide> : <SwiperSlide key={i+1}><img src={`${process.env.PUBLIC_URL}/assets/images/`+d.image} alt={d.alt} /></SwiperSlide>
					)}
					<SliderNavigation />
				</Swiper>
				<a href="" className="more">자세히 보기</a>
			</div>
	);
}
export default MainSlider;