import { useEffect } from "react";
import { useSelector } from "react-redux";

function Membership(){
    let data = useSelector(state => state.membership);

    useEffect(()=>{
        let bgVideo=document.getElementById("bgVideo");

        bgVideo.addEventListener("loadeddata", function(){
            bgVideo.play();
        });
    });
    return(
        <section id="sec2">
            <a href="">
                <video id="bgVideo" loop muted autoPlay playsInline poster={`${process.env.PUBLIC_URL}/assets/videos/`+data.poster} src={`${process.env.PUBLIC_URL}/assets/videos/`+data.video}></video>
                <div className="text">
                    <h2>{data.h2}</h2>
                    <p>{data.p}</p>
                </div>
            </a>
        </section>
    );
}
export default Membership;